// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Row, Col, Card } from 'react-materialize';
import Logos from '../logos/logos';
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';

const Inicio = () => (
  <Row>
    <Col m={3} s={12}>
      <Logos />
    </Col>
    <Col m={8} s={12}>
      <h5 className="subtitle center-align">Página Inicial</h5>
      <Card>
        <div>
          <p><b>Teste</b></p>
          <p>Bem vindo ao site sobre cancro europeu! Você pode navegar pelas diferentes seções através do cabeçalho logo acima.</p>
        </div>
      </Card>
    </Col>
  </Row>

);

export default Inicio;