/* eslint-disable jsx-a11y/alt-text */
// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Row, Col, Card } from 'react-materialize';
import imagem1 from '../../imagens/capas/C1.png';
import imagem2 from '../../imagens/capas/C2.jpg';
import imagem3 from '../../imagens/capas/C3.png';
import imagem4 from '../../imagens/capas/C4.jpg';
import imagem5 from '../../imagens/capas/C5.jpg';
import imagem6 from '../../imagens/capas/C6.jpg';
import { NavLink } from 'react-router-dom';
import './informacoes.css';


const Informacoes = () => (

  <Col m={8} s={12}>
    <h5 className="subtitle center-align">Informações</h5>
    <Row>
      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem1} className="card small responsive-img" />
          </div>

          <div class="info center-align">
            <b>Situação do Cancro Europeu em Santa Catarina</b>
            <h5><NavLink to="situacao-cancro">Visualizar</NavLink></h5>
          </div>
        </Card>
      </Col>

      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem2} className="card small responsive-img" />
          </div>

          <div className="info center-align">
            <b>Importância</b>
            <h5><NavLink to="importancia">Visualizar</NavLink></h5>
          </div>
        </Card>
      </Col>

      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem3} className="card small responsive-img" />
          </div>

          <div class="info center-align">
            <b>Histórico</b>
            <h5><NavLink to="historico">Visualizar</NavLink></h5>
          </div>
        </Card>
      </Col>

      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem4} className="card small responsive-img" />
          </div>

          <div class="info center-align">
            <b>Etiologia</b>
            <h5><a href="/">Visualizar</a></h5>
          </div>
        </Card>
      </Col>

      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem5} className="card small responsive-img" />
          </div>

          <div class="info center-align">
            <b>Sintomatologia</b>
            <h5><a href="/">Visualizar</a></h5>
          </div>
        </Card>
      </Col>

      <Col s={5} l={4}>
        <Card>
          <div className="card-image">
            <img src={imagem6} className="card small responsive-img" />
          </div>
          
          <div class="info center-align">
            <b>Epidemiologia</b>
            <h5><a href="/">Visualizar</a></h5>
          </div>
        </Card>
      </Col>
    </Row>
  </Col>
);



export default Informacoes;