/* eslint-disable jsx-a11y/alt-text */
// Importando o React
import React from "react";
// Importando os components necessários da lib react-materialize
import { Row, Col, Card } from 'react-materialize';
import imagem1 from '../../imagens/capasGaleria/G1.jpg';
import imagem2 from '../../imagens/capasGaleria/G2.JPG';
import imagem3 from '../../imagens/capasGaleria/G3.JPG';
import imagem4 from '../../imagens/capasGaleria/G4.JPG';
import imagem5 from '../../imagens/capasGaleria/G5.JPG';
import imagem6 from '../../imagens/capasGaleria/G6.JPG';
import { NavLink } from 'react-router-dom';
import './imagens.css';


const Imagens = () => (

    <Col m={8} s={12}>
        <h5 className="center-align">Galeria de Imagems</h5>
        <h6 className="subtitle">Sintomas</h6>
            <Row>
            <Col s={5} l={4}>
               <Card>
                <img src={imagem1} className="card small responsive-img"/>    
                
                <div class="info center-align">
                  
                <b>Sintomas do Cancro de Papel</b>
                <h5><NavLink to="cancro-papel">Visualizar</NavLink></h5>
                </div>
               </Card>
             </Col>

             <Col s={5} l={4}>
               <Card><img src={imagem2} className="card small responsive-img"/>    
                <div class="info center-align">
                  
                <b>Sintomas do Cancro em Frutos</b>
                <h5><a href="/importancia">Visualizar</a></h5>
                </div>
               </Card>
             </Col>

             <Col s={5} l={4}>
               <Card><img src={imagem3} className="card small responsive-img"/>    
                <div class="info center-align">
                  
                <b>Situação do Cancro na Brotação e Floração</b>
                <h5><a href="/">Visualizar</a></h5>
                </div>
               </Card>
             </Col>

             <Col s={5} l={4}>
               <Card><img src={imagem4} className="card small responsive-img"/>    
                <div class="info center-align">
                  
                <b>Sintomas do Cancro na Colheita</b>
                <h5><a href="/">Visualizar</a></h5>
                </div>
               </Card>
             </Col>

             <Col s={5} l={4}>
               <Card><img src={imagem5} className="card small responsive-img"/>    
                <div class="info center-align">
                  
                <b>Sintomas do Cancro na Poda</b>
                <h5><a href="/">Visualizar</a></h5>
                </div>
               </Card>
             </Col>

             <Col s={5} l={4}>
               <Card><img src={imagem6} className="card small responsive-img"/>    
                <div class="info center-align">
                  
                <b>Sintomas do Cancro na Queda de Folhas</b>
                <h5><a href="/">Visualizar</a></h5>
                </div>
               </Card>
             </Col>
            </Row>
    </Col>
);



export default Imagens;